.production {
  display: grid;
  margin-bottom: 58px;
  padding-bottom: 33px;
  border: 1px solid $red-light;
}

.production__title {
  position: relative;
  margin: 21px 0 26px;
  @include title;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
  color: $black;
}

.production__title::before {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 55px;
  height: 38px;
  content: "";
  background-image: url("../img/video.svg");
  background-repeat: no-repeat;
}

.production__video {
  display: flex;
  margin: 0 auto;
  margin-bottom: 35px;
}

.production__text {
  margin-bottom: 41px;
  @include text;
  font-size: 13px;
  line-height: 24px;
  color: $gray-dark;
}

.production__button {
  padding: 18px 11px;
  @include title;
  font-size: 18px;
  line-height: 16px;
  color: $gray-dark;
}

@media (min-width: $tablet-width) {
  .production__video {
    grid-row: 1;
  }
}

@media (min-width: $desktop-width) {
  .production {
    grid-template-columns: 2fr 1fr;
  }

  .production__video {
    grid-row: 1 / 4;
  }
}
